<template>
<div class="comment-page"> 
<Row class="row">
        <Col span="5" push='3'><img :src='canteenObj.canteenImage' class="canteen-img"/></Col>
        <Col span="12" class='row-b'><b >{{canteenObj.canteenName}}</b></Col>        
    </Row>
    <hr>
    <p class='pp'>评价</p>
    <template v-for="comment in commentList">
     <Card :key="comment.commentId" class='card'>
                <p>评论时间:{{comment.order.createdTime}}</p>
                <p>用户名:{{comment.user.userName}}</p>
                <p>评论内容:{{comment.commentContent}}</p>
            </Card>
    </template>
</div>     
</template>
<script>
import {selectManyComment,getOneCanteen} from '@/api/api.js'
export default {
    data(){
        return{
            canteenId:this.$route.query.canteenId,
            canteenObj:'',
            commentList:[]
        }
    },
    mounted(){
        let data={canteenId:this.canteenId}
        selectManyComment(data).then(res=>{
            this.commentList=res.data.data
        })
        getOneCanteen(data).then(res=>{
            this.canteenObj=res.data.data
        },err=>{
            this.$Message.error('网络错误')
        })
    }
}
</script>
<style lang="less">
.comment-page{   
    .pp{
        font-size:70px;
    }
    .card{
        width:1000px;
        height:200px;
        margin:20px 0px 20px 0px;
        p{
            margin-top:20px;
        }
    }
        .row{
   padding-top:10px;
   padding-bottom:10px;
  background-image: url('../../assets/4.png');
  .canteen-img{
       border-radius: 90px;
  }
  .row-b{
      font-size:30px;
      padding-top:30px;
  }
}
}
</style>

